.contacts {
  &__list {
    list-style: none;
    font-family: "Montserrat", "Arial", sans-serif;
    color: #9d9d9c;
    font-size: 16px;
    color: #9d9d9c;

    @media (max-width: 500px) {
      font-size: 11px;
    }
  }

  &__item {
    margin-bottom: 5px;

    &:nth-child(2) {
      margin-bottom: 25px;
    }
  }

  &__link {
    color: inherit;
    text-decoration: none;
  }
}
