h1,
h2,
h3,
h4,
h5,
h6,
ul {
  margin: 0;
  padding: 0;
}

body {
  font-family: "GothamPro", "Arial", sans-serif;
  font-size: 23px;
  display: flex;
  flex-direction: column;
  counter-reset: market-list, to-go-list, advantages-list;
  overflow-x: hidden;

  &.no-scroll {
    overflow: hidden;
  }
}

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;

  @media (max-width: 1400px) {
    padding-left: 30px;
    padding-right: 30px;
  }

  @media (max-width: 900px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.main-wrapper {
  flex-grow: 1;
  overflow-x: hidden;
  background-color: #f6f6f6;
}

.swiper-button-next:after,
.swiper-button-prev:after {
  font-size: 0;
  color: transparent;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
