.site-button {
  text-decoration: none;
  font-size: 24px;
  line-height: 1em;
  font-weight: 500;
  color: #fff;
  padding: 22px 40px 19px 40px;
  background-color: #caba9f;
  width: fit-content;
  text-transform: uppercase;
  cursor: pointer;
  transition: background-color 0.4s ease;
  border: none;

  @media (max-width: 900px) {
    padding: 15px 20px 11px 20px;
    font-size: 16px;
  }

  @media (max-width: 400px) {
    padding: 10px 20px;
    font-size: 11px;
  }

  &_small {
    padding: 12px 43px;
    font-size: 13px;

    @media (max-width: 400px) {
      padding: 7px 28px;
      font-size: 9px;
    }
  }

  &:hover {
    background-color: #afa188;
  }

  &:active {
    background-color: #857a69;
  }
}
