.advantages {
  background-color: #f6f6f6;

  &__wrap {
    padding-top: 48px;
    display: flex;
    justify-content: space-between;

    @media (max-width: 1400px) {
      flex-direction: column;
    }

    @media (max-width: 400px) {
      padding-top: 40px;
    }
  }

  &__name {
    display: flex;
    flex-direction: column;
    align-self: center;

    @media (max-width: 1400px) {
      text-align: center;
      margin-bottom: 50px;
    }
  }

  &__logo {
    font-size: 49px;
    line-height: 1em;
    font-weight: 400;
    color: #292929;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 8px;

    @media (max-width: 400px) {
      font-size: 36px;
    }
  }

  &__slogan {
    padding-left: 2px;
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 16px;
    color: #caba9f;
    margin: 0;
    text-transform: uppercase;

    @media (max-width: 1000px) {
      margin-bottom: 14px;
    }

    @media (max-width: 400px) {
      margin-bottom: 0;
      font-size: 9px;
    }
  }

  &__list {
    list-style: none;
    display: grid;
    grid-template-columns: repeat(3, 300px);
    column-gap: 80px;

    @media (max-width: 1550px) {
      grid-template-columns: repeat(3, 260px);
    }

    @media (max-width: 1400px) {
      grid-template-columns: repeat(3, 1fr);
    }

    @media (max-width: 900px) {
      grid-template-columns: repeat(1, 320px);
      justify-content: center;
      row-gap: 40px;
    }

    @media (max-width: 400px) {
      grid-template-columns: repeat(1, 180px);
      row-gap: 25px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    counter-increment: advantages-list;

    &::before {
      content: counter(advantages-list, decimal-leading-zero);
      font-size: 48px;
      font-weight: 400;
      color: #caba9f;
      margin-bottom: 12px;

      @media (max-width: 1000px) {
        font-size: 34px;
      }

      @media (max-width: 400px) {
        font-size: 26px;
        margin-bottom: 6px;
      }
    }
  }

  &__subtitle {
    font-size: 23px;
    font-weight: 400;
    color: #292929;
    text-transform: uppercase;
    margin-top: 0;
    margin-bottom: 10px;

    @media (max-width: 1000px) {
      font-size: 16px;
      margin-bottom: 6px;
    }

    @media (max-width: 400px) {
      font-size: 12px;
      margin-bottom: 4px;
    }
  }

  &__description {
    max-width: 80%;
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 16px;
    color: #9d9d9c;
    margin: 0;

    @media (max-width: 1550px) {
      max-width: 100%;
    }

    @media (max-width: 1400px) {
      max-width: 80%;
    }

    @media (max-width: 900px) {
      max-width: 100%;
    }

    @media (max-width: 400px) {
      font-size: 11px;
    }
  }
}
