.modal {
  position: fixed;
  top: 8%;
  // left: 50%;
  overflow: hidden;
  background-color: #292929;
  padding: 27px 23px 0 54px;
  box-sizing: border-box;
  z-index: 3;
  height: 80vh;
  width: 630px;
  transform: translateX(-50%);

  @media (max-width: 950px) {
    width: 530px;
  }

  @media (max-width: 600px) {
    width: 435px;
    padding: 20px 16px 0 47px;
  }

  @media (max-width: 500px) {
    padding: 20px 16px 0 30px;
    width: 360px;
    height: 60vh;
  }

  @media (max-width: 500px) {
    padding: 20px 16px 0 30px;
    width: 300px;
  }

  @media (max-width: 400px) {
    width: 290px;
    padding: 12px 16px 0 16px;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 27px;

    @media (max-width: 500px) {
      padding-bottom: 18px;
    }

    @media (max-width: 400px) {
      padding-bottom: 10px;
    }
  }

  &__header-logo {
    width: 25px;
    height: auto;
    display: block;
  }

  &__header-button {
    cursor: pointer;
    border: none;
    padding: 0;
    background-color: transparent;
    width: 30px;
    height: 30px;
    position: relative;
    transform: translate(6px, -15px);
    transition: opacity 0.2s ease;

    @media (max-width: 400px) {
      transform: translate(6px, -10px);
    }

    &::before,
    &::after {
      content: "";
    }

    &::before,
    &::after {
      position: absolute;
      left: 0;
      top: 50%;
      width: 100%;
      height: 2px;
      background-color: #caba9f;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }

    &:focus {
      outline: none;
    }

    &:hover {
      opacity: 0.7;
    }
  }

  &__list,
  &__sublist {
    list-style: none;
  }

  &__list {
    position: relative;
    height: calc(80vh - 150px);
    overflow: auto;

    @media (max-width: 500px) {
      height: calc(60vh - 100px);
    }
  }

  &__item {
    padding-top: 28px;
    padding-right: 11px;
    box-sizing: border-box;
    position: relative;

    @media (max-width: 400px) {
      padding-top: 18px;
    }

    &::before {
      content: "";
      width: calc(100% - 51px);
      height: 1px;
      background-color: #ad9c95;
      position: absolute;
      top: 0;
      left: 0;

      @media (max-width: 500px) {
        width: calc(100% - 30px);
      }
    }
  }

  &__title {
    color: #c4b19d;
    font-size: 23px;
    font-weight: 400;
    text-transform: uppercase;

    @media (max-width: 500px) {
      font-size: 20px;
    }

    @media (max-width: 400px) {
      font-size: 16px;
    }
  }

  &__sublist {
    padding-top: 24px;
    padding-bottom: 40px;
    padding-right: 63px;
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: #ffffff;

    @media (max-width: 500px) {
      font-size: 14px;
      padding-right: 15px;
    }

    @media (max-width: 400px) {
      font-size: 11px;
      padding-top: 18px;
      padding-bottom: 30px;
    }
  }

  &__subitem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    @media (max-width: 400px) {
      margin-bottom: 16px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__product,
  &__price {
    margin: 0;
  }

  &__price {
    color: #c6b69c;
    padding-left: 10px;
  }
}

/*
.custom-scroll
*/

.custom-scroll > .os-scrollbar {
  padding: 0;
}
.custom-scroll > .os-scrollbar-vertical {
  bottom: 11px;
  width: 11px;
}

.custom-scroll > .os-scrollbar-corner {
  height: 11px;
  width: 11px;
  background-color: transparent;
}
.custom-scroll > .os-scrollbar > .os-scrollbar-track {
  background: #000;
}

.custom-scroll > .os-scrollbar-vertical:hover > .os-scrollbar-track:before,
.custom-scroll > .os-scrollbar-vertical.active > .os-scrollbar-track:before {
  top: 0;
  bottom: 0;
  width: 100%;
  left: 0;
  margin-left: 0;
}
.custom-scroll > .os-scrollbar > .os-scrollbar-track > .os-scrollbar-handle {
  background: transparent;
}

.custom-scroll
  > .os-scrollbar-vertical
  > .os-scrollbar-track
  > .os-scrollbar-handle {
  max-height: 57px;
}
.custom-scroll
  > .os-scrollbar
  > .os-scrollbar-track
  > .os-scrollbar-handle:before {
  content: "";
  width: 100%;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #ad9c95;
}
.custom-scroll.os-host-scrolling
  > .os-scrollbar
  > .os-scrollbar-track
  > .os-scrollbar-handle:before {
  background-color: #454545;
}
.custom-scroll
  > .os-scrollbar
  > .os-scrollbar-track
  > .os-scrollbar-handle:hover:before,
.custom-scroll
  > .os-scrollbar
  > .os-scrollbar-track
  > .os-scrollbar-handle.active:before {
  background-color: #454545;
}

.custom-scroll
  > .os-scrollbar-vertical
  > .os-scrollbar-track
  > .os-scrollbar-handle {
  width: 100%;
  min-height: 16px;
}
.custom-scroll.os-host-transition
  > .os-scrollbar
  > .os-scrollbar-track
  > .os-scrollbar-handle:before {
  transition: background 0.3s;
}
.custom-scroll.os-host-transition > .os-scrollbar > .os-scrollbar-track:before {
  transition: margin 0.3s, height 0.3s, width 0.3s, top 0.3s, right 0.3s,
    bottom 0.3s, left 0.3s;
}
