.specialty-drinks {
  background-color: #f6f6f6;
  padding-bottom: 380px;

  @media (max-width: 1400px) {
    padding-bottom: 280px;
  }

  @media (max-width: 1100px) {
    margin-top: 600px;
  }

  @media (max-width: 900px) {
    padding-bottom: 180px;
  }

  @media (max-width: 600px) {
    margin-top: 280px;
  }

  @media (max-width: 767px) {
    margin-top: 200px;
  }

  @media (max-width: 600px) {
    margin-top: 0;
  }

  @media (max-width: 400px) {
    padding-bottom: 0;
  }

  &__container {
    max-width: 1370px;
    position: relative;

    @media (max-width: 1600px) {
      max-width: 1110px;
    }
  }

  &__wrap {
    padding-top: 217px;
    position: relative;

    @media (max-width: 1400px) {
      padding-top: 245px;
    }

    @media (max-width: 500px) {
      padding-top: 200px;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
    }

    &::after {
      width: 40%;
      height: 50%;
      border-bottom: 745px solid #c2bba8;
      border-left: 600px solid transparent;
      bottom: 0;
      right: 0;
      z-index: 0;

      @media (max-width: 1650px) {
        width: 17%;
      }

      @media (max-width: 1400px) {
        border-bottom: 685px solid #c2bba8;
      }

      @media (max-width: 900px) {
        border-bottom: 520px solid #c2bba8;
      }

      @media (max-width: 500px) {
        border-bottom: 400px solid #c2bba8;
        border-left: 355px solid transparent;
        width: 20%;
      }
    }

    &::before {
      width: 1000px;
      height: 700px;
      top: 0;
      right: 50px;
      z-index: 1;
      background-image: url(../img/cup-green-center.png);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;

      @media (max-width: 1650px) {
        right: -16%;
      }

      @media (max-width: 1250px) {
        right: -40%;
      }

      @media (max-width: 900px) {
        right: -285px;
        width: 600px;
        height: 500px;
      }

      @media (max-width: 400px) {
        width: 500px;
        height: 500px;
        right: -235px;
      }
    }
  }

  &__title {
    padding-bottom: 100px;
    font-size: 95px;
    max-width: 700px;
    position: relative;
    z-index: 1;

    @media (max-width: 900px) {
      max-width: 380px;
      padding-bottom: 70px;
    }

    @media (max-width: 400px) {
      padding-bottom: 57px;
    }
  }

  &__slider-wrap {
    background-color: rgba(0, 0, 0, 0.6);
    padding-top: 73px;
    padding-bottom: 100px;
    position: relative;
    z-index: 1;

    @media (max-width: 900px) {
      padding-top: 40px;
      padding-bottom: 40px;
    }

    @media (max-width: 400px) {
      padding-top: 28px;
      padding-bottom: 28px;
    }
  }

  &__slider-list {
    list-style: none;
    display: flex;
  }

  &__subtitle-wrapper {
    margin-bottom: 20px;
  }

  &__kind {
    display: block;
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 18px;
    font-weight: 300;
    color: #caba9f;
    margin-top: 0;
    margin-bottom: 20px;
    text-transform: uppercase;

    @media (max-width: 900px) {
      font-size: 18px;
      margin-bottom: 20px;
    }

    @media (max-width: 400px) {
      font-size: 9px;
      margin-bottom: 15px;
    }
  }

  &__name {
    font-size: 36px;
    font-weight: 400;
    color: #f8f8f8;
    margin-top: 0;
    text-transform: uppercase;
    max-width: 300px;

    @media (max-width: 900px) {
      font-size: 28px;
      margin-bottom: 20px;
    }

    @media (max-width: 400px) {
      font-size: 19px;
      margin-bottom: 15px;
      max-width: 200px;
    }
  }

  &__description {
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 17px;
    color: #9d9d9c;
    margin: 0;
    // max-width: 400px;

    @media (max-width: 900px) {
      font-size: 14px;
    }

    @media (max-width: 400px) {
      font-size: 11px;
      line-height: 16px;
      // max-width: 200px;
    }
  }

  &__slider-arrow {
    top: 50%;
    transform: translateY(-50%);

    &_prev {
      left: auto;
      right: calc(100% + 60px);

      @media (max-width: 1650px) {
        right: 100%;
      }
    }

    &_next {
      right: auto;
      left: calc(100% + 60px);

      @media (max-width: 1650px) {
        left: 100%;
      }
    }
  }
}
