.to-go {
  position: relative;
  background-color: #f6f6f6;

  &__wrap {
    padding-top: 80px;
    position: relative;

    @media (max-width: 1400px) {
      padding-top: 80px;
    }

    @media (max-width: 1100px) {
      padding: 80px 0 200px 0;
      margin: 0;
    }

    @media (max-width: 900px) {
      padding: 70px 0 0 0;
    }

    @media (max-width: 400px) {
      padding-top: 45px;
    }

    &::after {
      content: "";
      position: absolute;
      background-image: url(../img/two-lines.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      width: 67px;
      height: 67px;
      top: 126px;
      right: 123px;
      z-index: 2;

      @media (max-width: 1670px) {
        right: 15px;
      }

      @media (max-width: 1400px) {
        right: 0px;
      }

      @media (max-width: 1100px) {
        display: none;
      }
    }
  }

  &__title {
    position: absolute;
    top: 0;
    left: 90px;
    width: 480px;
    font-size: 109px;
    font-weight: 300;
    color: #caba9f;
    text-transform: uppercase;
    z-index: 2;

    @media (max-width: 1919px) {
      left: 50px;
    }

    @media (max-width: 1400px) {
      font-size: 80px;
      width: 460px;
    }

    @media (max-width: 1100px) {
      left: 30px;
    }

    @media (max-width: 900px) {
      font-size: 60px;
      margin-bottom: 31px;
      width: 330px;
      left: 20px;
    }

    @media (max-width: 400px) {
      font-size: 40px;
      width: 230px;
    }

    @media (max-width: 440px) {
      left: 20px;
    }
  }

  &__slider {
    max-width: 1600px;
    margin: 0 auto;

    @media (max-width: 1100px) {
      overflow: visible;
    }
  }

  &__slider-wrapper {
    list-style: none;
  }

  &__slide {
    position: relative;
    counter-increment: to-go-list;
  }

  &__slider-button {
    top: 50%;
    transform: translateY(-50%);

    &_prev {
      left: 42px;
    }

    &_next {
      right: 42px;
    }
  }

  &__img {
    width: 100%;
    height: auto;
    display: block;
  }

  &__sticker {
    padding: 20px 40px;
    background-color: #113c36;
    border-radius: 15px;
    position: absolute;
    top: 42%;
    right: 18%;
    z-index: 2;
    font-weight: 300;
    font-size: 40px;
    color: #fff;
    text-transform: uppercase;

    @media (max-width: 1600px) {
      font-size: 32px;
      padding: 15px 30px;
    }

    @media (max-width: 1100px) {
      display: none;
    }
  }

  &__content {
    position: absolute;
    bottom: 85px;
    left: 50%;
    z-index: 2;
    transform: translateX(-50%);
    display: flex;
    align-items: center;

    @media (max-width: 1560px) {
      left: 54%;
    }

    @media (max-width: 1230px) {
      bottom: 40px;
    }

    @media (max-width: 1170px) {
      bottom: 60px;
      flex-direction: column;
      left: 60px;
      transform: none;
    }

    @media (max-width: 1100px) {
      left: 30px;
    }

    @media (max-width: 767px) {
      top: 0;
      left: 0;
      position: relative;
      padding: 0 30px;
    }

    @media (max-width: 440px) {
      padding: 0 20px;
    }

    &::before {
      position: absolute;
      top: -70px;
      right: calc(100% + 64px);
      content: counter(to-go-list, decimal-leading-zero);
      font-size: 157px;
      font-weight: 300;
      color: #caba9f;

      @media (max-width: 1740px) {
        right: calc(100% + 48px);
      }

      @media (max-width: 1560px) {
        font-size: 120px;
        right: calc(100% + 32px);
      }

      @media (max-width: 1230px) {
        font-size: 100px;
        right: calc(100% + 24px);
      }

      @media (max-width: 1170px) {
        font-size: 48px;
        right: auto;
        left: 0;
        top: auto;
        bottom: calc(100% + 4px);
      }

      @media (max-width: 767px) {
        left: 30px;
        bottom: calc(100% + 10px);
      }

      @media (max-width: 440px) {
        left: 20px;
      }
    }
  }

  &__content-name {
    width: 360px;
    font-weight: 400;
    font-size: 48px;
    text-transform: uppercase;
    align-self: flex-end;
    margin-right: 114px;
    color: #caba9f;
    margin-top: 0;
    margin-bottom: 0;

    @media (max-width: 1740px) {
      margin-right: 80px;
    }

    @media (max-width: 1560px) {
      margin-right: 48px;
    }

    @media (max-width: 1230px) {
      margin-right: 32px;
    }

    @media (max-width: 1170px) {
      align-self: flex-start;
      width: 70%;
      margin-bottom: 6px;
    }

    @media (max-width: 900px) {
      font-size: 32px;
    }

    @media (max-width: 767px) {
      margin-top: 16px;
      margin-bottom: 18px;
    }

    @media (max-width: 600px) {
      width: 100%;
    }

    @media (max-width: 440px) {
      font-size: 20px;
      margin-bottom: 12px;
    }
  }

  &__content-description {
    font-family: "Montserrat", "Arial", sans-serif;
    color: #fbfbfb;
    margin-top: 0;
    margin-bottom: 0;
    width: 530px;

    @media (max-width: 1400px) {
      font-size: 18px;
      width: 400px;
    }

    @media (max-width: 1230px) {
      width: 360px;
    }

    @media (max-width: 1170px) {
      width: 100%;
    }

    @media (max-width: 767px) {
      color: #484848;
    }

    @media (max-width: 440px) {
      line-height: 16px;
      font-size: 11px;
    }
  }
}
