.market {
  background-color: #f6f6f6;

  padding-bottom: 460px;
  padding-top: 132px;

  @media (max-width: 1500px) {
    padding-bottom: 400px;
  }

  @media (max-width: 1100px) {
    padding-bottom: 350px;
  }

  @media (max-width: 900px) {
    padding-top: 100px;
    padding-bottom: 300px;
  }

  @media (max-width: 440px) {
    padding-top: 170px;
    padding-bottom: 170px;
  }

  &__title {
    padding-left: 40px;
    margin-bottom: 42px;
    position: relative;

    @media (max-width: 1919px) {
      padding-left: 0;
    }

    @media (max-width: 1750px) {
      margin-bottom: 150px;
    }

    @media (max-width: 750px) {
      margin-bottom: 120px;
    }

    @media (max-width: 400px) {
      margin-bottom: 40px;
    }

    &::after {
      content: "";
      position: absolute;
      background-image: url(../img/two-lines.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      width: 67px;
      height: 67px;
      top: 0;
      right: 60px;
      z-index: 2;

      @media (max-width: 1300px) {
        display: none;
      }
    }
  }

  &__slider-wrapper {
    width: 100%;
    position: relative;

    &::after {
      content: "";
      width: 100%;
      height: 324px;
      position: absolute;
      top: 0;
      left: 0;
      background-color: #e7e7e7;

      @media (max-width: 767px) {
        height: 200px;
      }

      @media (max-width: 540px) {
        height: 140px;
      }
    }
  }

  &__container {
    max-width: 1600px;
    margin: 0 auto;
    position: relative;
  }

  &__slider {
    overflow: visible;
  }

  &__slide {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 670px;
    counter-increment: market-list;

    @media (max-width: 1400px) {
      padding-top: 650px;
    }

    @media (max-width: 1170px) {
      padding-top: 420px;
    }

    @media (max-width: 900px) {
      padding-top: 400px;
    }

    @media (max-width: 770px) {
      padding-top: 320px;
    }

    @media (max-width: 600px) {
      padding-top: 240px;
    }

    @media (max-width: 440px) {
      padding-top: 160px;
    }
  }

  &__slider-btn {
    right: 10%;
    top: 30%;

    @media (max-width: 1300px) {
      right: 3%;
    }

    &_left {
      left: 10%;

      @media (max-width: 1300px) {
        left: 3%;
      }
    }
  }

  &__img {
    position: absolute;
    top: -150px;
    z-index: 3;
    width: 770px;
    height: auto;
    display: block;
    left: 50%;
    transform: translateX(-50%);

    @media (max-width: 900px) {
      width: 700px;
      top: -120px;
    }

    @media (max-width: 750px) {
      width: 600px;
    }

    @media (max-width: 650px) {
      width: 420px;
      top: -70px;
    }

    @media (max-width: 550px) {
      top: -40px;
    }

    @media (max-width: 400px) {
      width: 270px;
    }
  }

  &__content {
    position: absolute;
    bottom: 85px;
    left: 50%;
    z-index: 2;
    transform: translateX(-50%);
    display: flex;
    align-items: flex-start;

    @media (max-width: 1560px) {
      left: 54%;
    }

    @media (max-width: 1230px) {
      bottom: 40px;
    }

    @media (max-width: 1170px) {
      flex-direction: column;
      transform: none;
      position: static;
      padding-left: 60px;
    }

    @media (max-width: 1100px) {
      padding-left: 10px;
    }

    &::before {
      position: absolute;
      top: -70px;
      right: calc(100% + 64px);
      content: counter(market-list, decimal-leading-zero);
      font-size: 157px;
      font-weight: 300;
      color: #caba9f;

      @media (max-width: 1740px) {
        right: calc(100% + 48px);
      }

      @media (max-width: 1560px) {
        font-size: 120px;
        right: calc(100% + 32px);
      }

      @media (max-width: 1230px) {
        font-size: 100px;
        right: calc(100% + 24px);
      }

      @media (max-width: 1170px) {
        font-size: 92px;
        right: 0;
        left: auto;
        top: -120px;
      }

      @media (max-width: 767px) {
        top: -60px;
      }

      @media (max-width: 540px) {
        font-size: 40px;
      }
    }
  }

  &__content-name {
    width: 360px;
    font-weight: 400;
    font-size: 48px;
    text-transform: uppercase;
    align-self: flex-end;
    margin-right: 114px;
    color: #caba9f;
    margin-top: 0;
    margin-bottom: 0;

    @media (max-width: 1740px) {
      margin-right: 80px;
    }

    @media (max-width: 1560px) {
      margin-right: 48px;
    }

    @media (max-width: 1230px) {
      margin-right: 32px;
    }

    @media (max-width: 1170px) {
      align-self: flex-start;
      width: 70%;
      margin-bottom: 16px;
    }

    @media (max-width: 900px) {
      font-size: 32px;
    }

    @media (max-width: 767px) {
      padding-top: 4px;
      margin-bottom: 16px;
    }

    @media (max-width: 600px) {
      width: 100%;
    }

    @media (max-width: 440px) {
      font-size: 20px;
      margin-bottom: 12px;
    }
  }

  &__content-description {
    font-family: "Montserrat", "Arial", sans-serif;
    color: 393939;
    margin-top: 0;
    margin-bottom: 0;
    width: 530px;

    @media (max-width: 1400px) {
      font-size: 18px;
      width: 400px;
    }

    @media (max-width: 1230px) {
      width: 360px;
    }

    @media (max-width: 1170px) {
      width: 100%;
    }

    @media (max-width: 767px) {
      color: #484848;
    }

    @media (max-width: 440px) {
      font-size: 11px;
      line-height: 16px;
    }
  }

  &__slider-arrow {
    top: 168px;

    &_prev {
      left: 42px;
    }

    &_next {
      right: 42px;
    }
  }

  .swiper-slide {
    opacity: 0;
    transition: opacity 0.4s;
  }

  .swiper-slide-active {
    opacity: 1;
  }
}
