.values {
  background-color: #f6f6f6;

  &__wrap {
    padding-top: 355px;
    padding-bottom: 340px;

    @media (max-width: 1400px) {
      padding-top: 250px;
      padding-bottom: 240px;
    }

    @media (max-width: 900px) {
      padding-top: 150px;
      padding-bottom: 140px;
    }

    @media (max-width: 400px) {
      padding-top: 90px;
      padding-bottom: 120px;
    }
  }

  &__subtitle {
    margin-right: 58px;
    margin-bottom: 31px;
  }

  &__text {
    @media (max-width: 900px) {
      margin-bottom: 30px;
    }

    @media (max-width: 400px) {
      margin-bottom: 11px;
    }
  }
}
