.application {
  padding-left: 0;
  background-color: #e6e6e6;

  @media (max-width: 1400px) {
    padding-left: 0;
    padding-right: 0;
  }

  @media (max-width: 900px) {
    padding-left: 0;
    padding-right: 0;
  }

  &__wrap {
    display: flex;

    @media (max-width: 1400px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__map {
    filter: grayscale(1);
    background-color: #2a2a2a;
    position: relative;
    flex: 1;
    max-height: 800px;

    @media (max-width: 1800px) {
      min-width: 1000px;
    }

    @media (max-width: 1600px) {
      min-width: 800px;
    }

    @media (max-width: 1400px) {
      width: 100%;
      height: 600px;
      flex: auto;
      min-width: auto;
    }

    @media (max-width: 900px) {
      height: 450px;
    }

    @media (max-width: 400px) {
      height: 310px;
    }
  }

  &__form-wrap {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding-top: 141px;
    padding-bottom: 85px;
    padding-left: 114px;
    width: 100%;
    max-width: 680px;

    @media (max-width: 1400px) {
      flex-direction: column;
      align-items: center;
      padding-right: 28px;
      padding-left: 28px;
    }

    @media (max-width: 500px) {
      padding-top: 34px;
      padding-bottom: 30px;
    }
  }

  &__title {
    font-size: 37px;
    font-weight: 400;
    color: #1a1a1a;
    text-transform: uppercase;
    margin-top: 0;
    margin-bottom: 32px;

    @media (max-width: 900px) {
      font-size: 30px;
    }

    @media (max-width: 500px) {
      font-size: 24px;
      margin-bottom: 32px;
    }

    @media (max-width: 320px) {
      align-self: flex-start;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    width: 415px;
    margin-right: auto;

    @media (max-width: 1400px) {
      align-items: center;
      margin-right: 0;
      max-width: 415px;
      width: auto;
    }
  }

  &__input,
  &__textarea {
    width: 100%;
    box-sizing: border-box;
    background-color: transparent;
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 19px;
    color: #1a1a1a;

    @media (max-width: 900px) {
      font-size: 16px;
    }

    @media (max-width: 500px) {
      font-size: 11px;
    }

    &::placeholder {
      color: #8d8d8d;
    }

    &:focus {
      outline: none;

      &::placeholder {
        color: transparent;
      }
    }
  }

  &__input {
    border: none;
    border-bottom: 2px solid #c1c1c1;
    height: 35px;
    margin-bottom: 30px;

    @media (max-width: 500px) {
      margin-bottom: 24px;
      height: 20px;
    }

    &:focus {
      padding-left: 11px;
    }
  }

  &__textarea {
    margin-bottom: 42px;
    border: 2px solid #c1c1c1;
    padding: 10px 11px;
    min-height: 100px;
    resize: none;

    @media (max-width: 500px) {
      margin-bottom: 24px;
    }
  }

  &__checkbox-wrap {
    display: flex;
    margin-bottom: 34px;

    @media (max-width: 500px) {
      margin-bottom: 28px;
    }
  }

  &__checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }

  &__checkbox + &__label::before {
    content: "";
    display: inline-block;
    width: 11px;
    height: 11px;
    background-color: #cccccc;
    margin-right: 7px;
    cursor: pointer;
  }

  &__checkbox:checked + &__label::before {
    background-image: url(../img/check.svg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 60%;
  }

  &__label {
    font-family: "OpenSans";
    font-size: 11px;
    line-height: 14px;
    color: #747474;
  }

  &__link {
    color: inherit;
  }

  &__button {
    @media (max-width: 400px) {
      align-self: flex-start;
    }
  }
}
