.statement {
  display: flex;
  margin: 0 auto;
  min-height: 375px;
  max-width: 1100px;

  @media (max-width: 1400px) {
    max-width: 900px;
  }

  @media (max-width: 900px) {
    flex-direction: column;
    max-width: 400px;
    min-height: auto;
  }

  &__text-wrap {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  &__text {
    font-family: "Montserrat", "Arial", sans-serif;
    color: #484848;
    margin-bottom: 50px;
    margin-top: 0;

    @media (max-width: 900px) {
      font-size: 16px;
      line-height: 16px;
    }

    @media (max-width: 400px) {
      font-size: 11px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}
