.site-title {
  font-size: 109px;
  color: #caba9f;
  font-weight: 300;
  text-transform: uppercase;
  margin-top: 0;

  &_bold {
    font-weight: 400;
  }

  &_small {
    font-size: 98px;
  }

  @media (max-width: 1400px) {
    font-size: 80px;
  }

  @media (max-width: 900px) {
    font-size: 60px;
  }

  @media (max-width: 700px) {
    font-size: 50px;
  }

  @media (max-width: 400px) {
    font-size: 40px;
  }
}
