.footer {
  background-color: #2a2a2a;
  font-family: "Montserrat", "Arial", sans-serif;

  &__container {
    padding: 58px 138px 100px 64px;
    display: flex;

    @media (max-width: 1100px) {
      padding: 58px 50px 100px 50px;
    }

    @media (max-width: 500px) {
      padding: 37px 24px 43px 34px;
    }
  }

  &__wrap {
    display: flex;

    @media (max-width: 650px) {
      flex-direction: column-reverse;
      justify-content: space-between;
    }
  }

  &__social {
    margin-right: 125px;
    padding-top: 10px;

    @media (max-width: 1100px) {
      margin-right: 90px;
    }

    @media (max-width: 650px) {
      margin-right: 0;
    }
  }

  &__category-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 42px;
    row-gap: 35px;
    margin-left: auto;
    list-style: none;

    @media (max-width: 900px) {
      grid-template-columns: repeat(1, 1fr);
    }

    @media (max-width: 500px) {
      row-gap: 19px;
    }
  }

  &__category-item {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    font-size: 16px;

    @media (max-width: 500px) {
      font-size: 11px;
    }

    &:last-child {
      grid-column: 1/3;
      grid-row: 2/3;

      @media (max-width: 900px) {
        grid-column: auto;
        grid-row: auto;
      }
    }
  }

  &__category-name {
    color: #caba9f;
    margin-top: 0;
    margin-bottom: 5px;
  }

  &__category-link {
    color: #9d9d9c;
  }
}
