.drinks {
  padding-bottom: 481px;
  position: relative;
  background-color: #f6f6f6;

  @media (max-width: 1100px) {
    padding-bottom: 555px;
  }

  @media (max-width: 900px) {
    padding-bottom: 400px;
  }

  @media (max-width: 400px) {
    padding-bottom: 300px;
  }

  @media (max-width: 320px) {
    display: none;
  }

  &::before {
    content: "";
    position: absolute;
    top: 1000px;
    right: 0;
    z-index: 2;
    width: 100%;
    height: 18px;
    background-color: #c4b19d;
    box-shadow: 0 22px 0 0px #c4b19d;

    @media (max-width: 1400px) {
      top: 800px;
    }

    @media (max-width: 1100px) {
      top: 650px;
    }

    @media (max-width: 900px) {
      top: 550px;
    }

    @media (max-width: 600px) {
      height: 13px;
      box-shadow: 0 17px 0 0px #c4b19d;
      top: 500px;
    }

    @media (max-width: 400px) {
      top: 400px;
    }
  }

  &__wrap {
    padding-top: 60px;
    padding-bottom: 325px;
    position: relative;
    z-index: 2;

    @media (max-width: 1400px) {
      padding-bottom: 245px;
    }

    @media (max-width: 400px) {
      padding-bottom: 145px;
    }

    &::after {
      content: "";
      position: absolute;
      background-image: url(../img/two-lines.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      width: 67px;
      height: 67px;
      top: 126px;
      right: 110px;
      z-index: 2;

      @media (max-width: 1100px) {
        display: none;
      }
    }

    &::before {
      content: "";
      position: absolute;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      z-index: 2;
      width: 840px;
      height: 840px;
      background-image: url(../img/cups.png);
      left: 0;
      bottom: -450px;

      @media (max-width: 1400px) {
        bottom: -400px;
        left: -100px;
        width: 740px;
        height: 740px;
      }

      @media (max-width: 1100px) {
        bottom: -700px;
      }

      @media (max-width: 900px) {
        width: 640px;
        height: 640px;
        bottom: -550px;
      }

      @media (max-width: 600px) {
        width: 440px;
        height: 440px;
        bottom: -360px;
        left: -50px;
      }

      @media (max-width: 400px) {
        width: 340px;
        height: 340px;
        bottom: -325px;
      }
    }
  }

  &__title {
    padding-left: 40px;
    width: 760px;
    margin-bottom: 65px;

    @media (max-width: 1919px) {
      padding-left: 0;
    }

    @media (max-width: 1100px) {
      transform: translateY(-150px);
    }

    @media (max-width: 900px) {
      width: 560px;
      transform: translateY(-125px);
    }

    @media (max-width: 700px) {
      width: 560px;
      transform: translateY(-115px);
    }

    @media (max-width: 400px) {
      width: 460px;
      transform: translateY(-105px);
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    width: 45%;
    margin-left: auto;

    @media (max-width: 1600px) {
      width: 50%;
    }

    @media (max-width: 1100px) {
      position: absolute;
      top: 1100px;
      left: 80px;
      z-index: 3;
    }

    @media (max-width: 900px) {
      left: 40px;
      top: 900px;
    }

    @media (max-width: 600px) {
      top: 770px;
    }

    @media (max-width: 440px) {
      left: 15px;
    }

    @media (max-width: 400px) {
      top: 630px;
    }
  }

  &__content-subtitle {
    margin-bottom: 87px;

    @media (max-width: 1100px) {
      margin-bottom: 44px;
    }

    @media (max-width: 767px) {
      display: none;
    }
  }

  &__content-btn {
    margin-left: 11px;
    z-index: 3;

    @media (max-width: 900px) {
      margin-left: 0;
    }
  }

  &__bg-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 73%;
    object-fit: cover;
  }

  &__modal {
    left: 200%;
    transition: left 0.4s ease-in-out;

    &_opened {
      left: 50%;
    }
  }
}
