.header {
  $root: &;
  background-color: #292929;
  position: relative;

  &__container {
    position: relative;
    z-index: 2;

    @media (max-width: 900px) {
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 55px;
    padding-bottom: 35px;
    border-bottom: 1px solid #c4b29d;

    @media (max-width: 900px) {
      padding: 30px;
    }

    @media (max-width: 400px) {
      padding: 23px 10px;
    }
  }

  &__company-name {
    display: block;
    width: 153px;
    height: auto;

    @media (max-width: 900px) {
      width: 120px;
    }

    @media (max-width: 400px) {
      width: 70px;
    }
  }

  &__nav-button {
    display: none;
    position: relative;
    cursor: pointer;
    border: none;
    padding: 0;
    width: 24px;
    height: 18px;
    background-color: transparent;
    z-index: 1;

    @media (max-width: 900px) {
      display: block;
    }

    @media (max-width: 375px) {
      width: 17px;
      height: 12px;
    }

    &::before,
    &::after {
      content: "";
    }

    &::before,
    &::after,
    #{$root}__button-line {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #ffffff;
      transition: transform 0.4s ease;
    }

    &::before {
      top: 0;
    }

    &::after {
      bottom: 0;
    }

    &:focus {
      outline: none;
    }
  }

  &__button-line {
    top: 50%;
    transform: translateY(-50%);
  }

  &__nav {
    @media (max-width: 900px) {
      min-height: 100vh;
      position: absolute;
      top: 0;
      right: -100%;
      background-color: #292929;
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      padding-top: 60px;
      padding-left: 34px;
      padding-bottom: 100px;
      min-width: 320px;
      box-sizing: border-box;
      transition: right 0.4s ease-in-out;

      &_opened {
        right: 0;
      }
    }

    @media (max-width: 400px) {
      padding-top: 48px;
      padding-left: 34px;
      padding-bottom: 100px;
      min-width: 200px;
    }
  }

  &__list {
    list-style: none;
    display: flex;
    align-items: center;

    @media (max-width: 900px) {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 70px;
    }
  }

  &__item {
    font-family: Montserrat, "Arial", sans-serif;
    font-weight: 400;
    font-size: 20px;
    color: #fff;
    margin-right: 20px;
    padding-bottom: 1px;
    position: relative;
    transition: color 0.4s ease;

    @media (max-width: 900px) {
      margin-right: 0;
      margin-bottom: 25px;
    }

    @media (max-width: 400px) {
      font-size: 18px;
    }

    &::after {
      content: "";
      position: absolute;
      opacity: 0;
      bottom: 0;
      left: 0;
      background-color: #c4b29d;
      height: 1px;
      width: 100%;
      transition: opacity 0.4s ease;
    }

    &:hover {
      color: #c4b29d;

      &::after {
        opacity: 1;
      }
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    color: inherit;
    text-decoration: none;
  }

  &__nav-social,
  &__nav-contacts {
    display: none;

    @media (max-width: 900px) {
      display: flex;
    }
  }

  &__nav-social {
    @media (max-width: 900px) {
      margin-bottom: 60px;
    }
  }

  &__contacts-item {
    @media (max-width: 500px) {
      font-size: 14px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 110px;
    padding-top: 86px;

    @media (max-width: 900px) {
      padding-top: 73px;
    }

    @media (max-width: 400px) {
      padding: 63px 10px 157px 10px;
    }
  }

  &__content-img {
    width: 100px;
    height: auto;
    display: block;

    @media (max-width: 900px) {
      width: 70px;
    }

    @media (max-width: 400px) {
      width: 50px;
    }
  }

  &__content-text {
    font-size: 32px;
    line-height: 1.5em;
    font-weight: 500;
    color: #f6f6f6;
    text-transform: uppercase;
    margin-top: 70px;
    margin-bottom: 0;
    text-align: center;

    @media (max-width: 900px) {
      font-size: 24px;
    }

    @media (max-width: 400px) {
      margin-top: 48px;
      font-size: 15px;
    }
  }

  &__bg-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
